
import { defineComponent, PropType } from "vue";

export class Group {
  constructor(
    public id: number,
    public name: string,
    public managersCount: number,
    public avatar: string
  ) {}
}

export default defineComponent({
  props: {
    active: {
      type: Boolean,
      default: false
    },
    group: {
      type: Object as PropType<Group>,
      required: true
    }
  },
  name: "headline-group-box"
});
