
import { defineComponent } from "vue";

import HeadlineGroupBox, { Group } from "@/components/group/group-box.vue";
import HeadlineButton from "@/components/form/button.vue";
import HeadlineInput from "@/components/form/input.vue";
import HeadlineModalBox from "@/components/modal/modal-box.vue";
import { mapActions, mapMutations, mapState } from "vuex";

export default defineComponent({
  name: "Groups",
  data() {
    return {
      groupId: "",
      groupAccessToken: "",
      creatingGroup: false,
      groups: [
        {
          id: 1,
          name: "Headline School | Обучение рисованию аниме",
          managersCount: 3,
          avatar: "https://img.gameme.eu/images/LJRryEShuPXirdoDdR6KDX.jpg"
        },
        {
          id: 2,
          name: "Headline School | Обучение жарки картошычки ммм картошычка",
          managersCount: 26,
          avatar: "https://img.gameme.eu/images/LJRryEShuPXirdoDdR6KDX.jpg"
        }
      ] as Group[],
      activeGroup: null
    };
  },
  methods: {
    activateGroup(group: Group) {
      this.activeGroup = (group as any);
    },
    toggleCreatingGroup() {
      this.creatingGroup = !this.creatingGroup;
    },
    async handleCreateGroup() {
      const newGroup = await this.createGroup({
        jwtAccessToken: this.accessToken,
        groupAccessToken: this.groupAccessToken,
        groupId: this.groupId
      });

      this.addGroup(newGroup);
      console.log(this.groups);
    },
    ...mapActions("group", ["createGroup"]),
    ...mapMutations("group", ["setGroups", "addGroup"])
  },
  computed: {
    ...mapState("auth", ["accessToken"]),
    ...mapState("group", ["groups"])
  },
  components: {
    HeadlineGroupBox,
    HeadlineButton,
    HeadlineInput,
    HeadlineModalBox
  }
});
