<template>
  <div @scroll="onScroll" class="chat" ref="root">
    <div
      v-for="message of messages"
      :key="message.id"
      class="chat__message-box"
      :class="{ unreaded: !message.isReaded, owner: message.isOwner }"
    >
      <headline-message
        :message="message"
        class="chat__message"
        :reversed="message.isOwner"
      />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

import HeadlineMessage, { Message } from "./message.vue";

export class ChatMessage extends Message {
  constructor(
    public id: number,
    public avatar: string,
    public name: string,
    public surname: string,
    public date: Date,
    public text: string,
    public userId: number,
    public dialogId: number,
    public isReaded: boolean,
    public isOwner: boolean
  ) {
    super(id, avatar, name, surname, date, text, userId, dialogId);
  }
}

export default defineComponent({
  props: {
    messages: {
      type: Array as () => ChatMessage[],
      required: true
    }
  },
  data() {
    return {
      lastScrollHeight: 0,
      scrollTop: 0
    };
  },
  methods: {
    onScroll(event: Event) {
      const root = event.target as HTMLDivElement;
      this.scrollTop = root.scrollTop;
    }
  },
  mounted() {
    const root = this.$refs.root as HTMLDivElement;
    this.scrollTop = root.scrollHeight - root.clientHeight;
    root.scrollTop = this.scrollTop;
    this.lastScrollHeight = root.scrollHeight;
  },
  updated() {
    const root = this.$refs.root as HTMLDivElement;
    if (
      root.scrollTop === this.lastScrollHeight - root.clientHeight ||
      this.lastScrollHeight > root.scrollHeight
    ) {
      root.scrollTop = root.scrollHeight - root.clientHeight;
      this.scrollTop = root.scrollTop;
      this.lastScrollHeight = root.scrollHeight;
    }
  },
  name: "headline-chat",
  components: {
    HeadlineMessage
  }
});
</script>

<style lang="scss" scoped>
.chat {
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
  }

  &__message-box {
    display: flex;
    align-items: center;
    width: 80%;
    margin-bottom: 15px;
    padding: 30px 15px;
    background: map-get($light-theme-colors, light-white);
    border-radius: 5px;

    &:last-of-type {
      margin-bottom: 0;
    }

    &.owner {
      margin-left: auto;
      justify-content: flex-end;
    }

    &.unreaded {
      background: linear-gradient(
          0deg,
          rgba(0, 0, 255, 0.47) -1.79%,
          rgba(0, 0, 255, 0.0001) 101.79%
        ),
        linear-gradient(0deg, #fafafa, #fafafa);
    }
  }

  &__message {
    height: 70px;
  }
}
</style>
