import Axios from "axios";
import { Actions, Getters, Module, Mutations } from "vuex-smart-module";

class UserState {
  user: any = null;
}

class UserMutations extends Mutations<UserState> {
  setUser(payload: any) {
    this.state.user = payload;
  }
}

class UserGetters extends Getters<UserState> {}

class UserActions extends Actions<UserState, UserGetters, UserMutations> {
  async fetchUser({ jwtAccessToken, vkUserId }: any): Promise<any> {
    console.log(jwtAccessToken);
    console.log(vkUserId);
    const res = await Axios.get(`http://127.0.0.1:3000/user/${vkUserId}`, {
      responseType: "json",
      headers: {
        Authorization: "Bearer " + jwtAccessToken
      }
    });

    return res.data;
  }
}

export const user = new Module({
  state: UserState,
  actions: UserActions,
  mutations: UserMutations,
  getters: UserGetters
});
