
import { defineComponent } from "vue";

import HeadlineMessage, { Message } from "./message.vue";

export class ChatMessage extends Message {
  constructor(
    public id: number,
    public avatar: string,
    public name: string,
    public surname: string,
    public date: Date,
    public text: string,
    public userId: number,
    public dialogId: number,
    public isReaded: boolean,
    public isOwner: boolean
  ) {
    super(id, avatar, name, surname, date, text, userId, dialogId);
  }
}

export default defineComponent({
  props: {
    messages: {
      type: Array as () => ChatMessage[],
      required: true
    }
  },
  data() {
    return {
      lastScrollHeight: 0,
      scrollTop: 0
    };
  },
  methods: {
    onScroll(event: Event) {
      const root = event.target as HTMLDivElement;
      this.scrollTop = root.scrollTop;
    }
  },
  mounted() {
    const root = this.$refs.root as HTMLDivElement;
    this.scrollTop = root.scrollHeight - root.clientHeight;
    root.scrollTop = this.scrollTop;
    this.lastScrollHeight = root.scrollHeight;
  },
  updated() {
    const root = this.$refs.root as HTMLDivElement;
    if (
      root.scrollTop === this.lastScrollHeight - root.clientHeight ||
      this.lastScrollHeight > root.scrollHeight
    ) {
      root.scrollTop = root.scrollHeight - root.clientHeight;
      this.scrollTop = root.scrollTop;
      this.lastScrollHeight = root.scrollHeight;
    }
  },
  name: "headline-chat",
  components: {
    HeadlineMessage
  }
});
