
import { defineComponent, PropType } from "vue";

import HeadlineBlankButton from "@/components/form/blank-button.vue";

export interface Info {
  avatar: string;
  lastSeen: Date;
  name: string;
  surname: string;
  link: string;
}

export default defineComponent({
  props: {
    info: {
      type: Object as PropType<Info>,
      required: true
    },
    showLastSeen: {
      type: Boolean,
      default: false
    },
    showLink: {
      type: Boolean,
      default: false
    }
  },
  name: "headline-profile-info",
  mounted() {
    const avatar = this.$refs.avatar as HTMLInputElement;
    this.imageHeight = avatar.clientHeight + "px";
  },
  data() {
    return {
      imageHeight: "0"
    };
  },
  methods: {
    copyLink() {
      const input = this.$refs.input as HTMLInputElement;
      input.value = this.info.link;
      input.select();
      document.execCommand("copy");
    }
  },
  components: {
    HeadlineBlankButton
  }
});
