      :alt="'Avatar of' + info.name"
      :style="{ width: imageHeight }"
      class="profile-info__avatar"
    />
    <div class="profile-info__wrapper">
      <span class="profile-info__last-seen" v-if="showLastSeen">
        Был онлайн {{ info.lastSeen }} минут назад
      </span>
      <span class="profile-info__full-name">
        {{ info.name }} {{ info.surname }}
      </span>
      <span class="profile-info__link" v-if="showLink">
        <a class="profile-info__link-text" :href="info.link">{{ info.link }}</a>
        <headline-blank-button
          @click="copyLink"
          class="profile-info__link-button"
        >
          <svg
