
import { defineComponent } from "vue";

export default defineComponent({
  name: "headline-dropdown-button",
  data() {
    return {
      isOpen: false,
      isClicked: false
    };
  },
  methods: {
    onClicked() {
      this.isClicked = true;
      (this.$refs as any).div.addEventListener("animationend", () => {
        this.isClicked = false;
      });
    },
    toggleOpen() {
      this.isOpen = !this.isOpen;
    }
  }
});
