    },
    activateDialog(dialog: Dialog) {
      this.activeDialog = dialog;
      this.activeUser = this.users.find(u => u.id === dialog.userId) || null;
      this.activeMessages = this.messages
        .filter(m => m.dialogId === dialog.id)
        .sort((m1, m2) => m1.id - m2.id);
    },
    toggleTag(tag: Tag, dialog: Dialog) {
      const tagIdx = dialog.tags.findIndex(t => t === tag);

      if (tagIdx !== -1) {
        dialog.tags.splice(tagIdx, 1);
      } else {
        dialog.tags.push(tag);
      }
    }
