import { createApp } from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import { store } from "./store";

createApp(App)
  .use(store)
  .use(router)
  .mount("#app");

function checkAuthorized() {
  return store.state.user.user;
}

async function authorizeByCode(code: string): Promise<boolean> {
  const {
    jwtAccessToken,
    user: { accessToken, vkId }
  } = await store.dispatch("auth/fetchAccessToken", code);
  if (!jwtAccessToken) {
    return false;
  } else {
    store.commit("auth/setAccessToken", accessToken);
    const user = await store.dispatch("user/fetchUser", {
      jwtAccessToken,
      vkUserId: vkId
    });
    store.commit("user/setUser", user);
    return true;
  }
}

router.beforeEach(async (to, from, next) => {
  if (to.name === "Login") {
    next();
    return;
  }

  if (checkAuthorized()) {
    next();
    return;
  }

  const code = to.query.code;
  if (code) {
    try {
      const isAllow = await authorizeByCode(code.toString());

      if (isAllow) {
        next();
        return;
      } else {
        next("/");
        return;
      }
    } catch (e) {
      next("/login");
      return;
    }
  }

  next("/login");
});
