<template>
  <div class="groups">
    <div class="groups__list groups-list">
      <span class="groups-list__title">Паблики</span>
      <headline-group-box
        v-for="group of groups"
        :key="group.id"
        :group="group"
        :active="group === activeGroup"
        @click="activateGroup(group)"
        class="groups-list__item"
      ></headline-group-box>
      <headline-button @click="toggleCreatingGroup" class="groups-list__button">
        <template v-slot:text>
          Добавить паблик
        </template>
      </headline-button>
    </div>

    <div class="groups__info groups-info"></div>

    <teleport to="#app-modal-wrapper">
      <headline-modal-box
        v-if="creatingGroup"
        class="groups__new-group-modal new-group-modal"
      >
        <span class="new-group-modal__title">Добавить группу</span>
        <headline-input
          :value="groupId"
          @input="groupId = $event.target.value"
          placeholder="Вставте id группы ВКонтакте"
          class="new-group-modal__input"
        ></headline-input>
        <headline-input
          :value="groupAccessToken"
          @input="groupAccessToken = $event.target.value"
          placeholder="Вставте секретный ключ"
          class="new-group-modal__input"
        ></headline-input>
        <headline-button
          @click="handleCreateGroup"
          class="new-group-modal__button"
        >
          <template v-slot:text>
            Добавить
          </template>
        </headline-button>
      </headline-modal-box>
    </teleport>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

import HeadlineGroupBox, { Group } from "@/components/group/group-box.vue";
import HeadlineButton from "@/components/form/button.vue";
import HeadlineInput from "@/components/form/input.vue";
import HeadlineModalBox from "@/components/modal/modal-box.vue";
import { mapActions, mapMutations, mapState } from "vuex";

export default defineComponent({
  name: "Groups",
  data() {
    return {
      groupId: "",
      groupAccessToken: "",
      creatingGroup: false,
      groups: [
        {
          id: 1,
          name: "Headline School | Обучение рисованию аниме",
          managersCount: 3,
          avatar: "https://img.gameme.eu/images/LJRryEShuPXirdoDdR6KDX.jpg"
        },
        {
          id: 2,
          name: "Headline School | Обучение жарки картошычки ммм картошычка",
          managersCount: 26,
          avatar: "https://img.gameme.eu/images/LJRryEShuPXirdoDdR6KDX.jpg"
        }
      ] as Group[],
      activeGroup: null
    };
  },
  methods: {
    activateGroup(group: Group) {
      this.activeGroup = (group as any);
    },
    toggleCreatingGroup() {
      this.creatingGroup = !this.creatingGroup;
    },
    async handleCreateGroup() {
      const newGroup = await this.createGroup({
        jwtAccessToken: this.accessToken,
        groupAccessToken: this.groupAccessToken,
        groupId: this.groupId
      });

      this.addGroup(newGroup);
      console.log(this.groups);
    },
    ...mapActions("group", ["createGroup"]),
    ...mapMutations("group", ["setGroups", "addGroup"])
  },
  computed: {
    ...mapState("auth", ["accessToken"]),
    ...mapState("group", ["groups"])
  },
  components: {
    HeadlineGroupBox,
    HeadlineButton,
    HeadlineInput,
    HeadlineModalBox
  }
});
</script>

