<template>
  <div
    @click="$emit('click')"
    class="message-box"
    :class="{ unreaded: dialog.newMessages !== 0, active }"
  >
    <headline-message
      :message="dialog.lastMessage"
      :showAvatar="true"
      :showFullName="true"
    />
    <headline-tags
      @toggle-tag="$emit('toggle-tag', $event)"
      :tags="dialog.tags"
      class="message-box__tags"
    />
    <headline-button-circle
      :class="{ disabled: dialog.newMessages === 0 }"
      class="message-box__action"
    >
      <span v-if="dialog.newMessages >= 100" class="message-box__action-text">
        99+
      </span>
      <span v-else class="message-box__action-text">
        {{ dialog.newMessages }}
      </span>
    </headline-button-circle>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";

import HeadlineMessage, { Message } from "./message.vue";
import HeadlineButtonCircle from "../form/button-circle.vue";
import HeadlineTags, { Tag } from "./tags.vue";

export class Dialog {
  constructor(
    public id: number,
    public lastMessage: Message,
    public tags: Tag[],
    public newMessages: number,
    public userId: number
  ) {}
}

export default defineComponent({
  props: {
    active: {
      type: Boolean,
      default: false
    },
    dialog: {
      type: Object as PropType<Dialog>,
      required: true
    }
  },
  name: "headline-message-box",
  components: {
    HeadlineMessage,
    HeadlineButtonCircle,
    HeadlineTags
  }
});
</script>

<style lang="scss" scoped>
