  display: flex;
  align-items: center;
  padding: 10px 15px;
  border-radius: 3px;
  background: map-get($light-theme-colors, "light-white");

  &.unreaded {
    background: linear-gradient(
        0deg,
        rgba(0, 0, 255, 0.47) -1.79%,
        rgba(0, 0, 255, 0.0001) 101.79%
      ),
      linear-gradient(0deg, #fafafa, #fafafa);
  }

  &.active,
  &.unreaded.active {
    background: linear-gradient(
        0deg,
        rgba(0, 54, 255, 0.47) 0%,
        rgba(232, 139, 255, 0.47) 100%
      ),
      map-get($light-theme-colors, "light-white");
  }

  &__tags {
    margin-left: auto;
  }

