
import { defineComponent, PropType } from "vue";

import dateFilter from "@/filters/date";

export class Message {
  constructor(
    public id: number,
    public avatar: string,
    public name: string,
    public surname: string,
    public date: Date,
    public text: string,
    public userId: number,
    public dialogId: number
  ) {}
}

export default defineComponent({
  props: {
    message: {
      type: Object as PropType<Message>,
      required: true
    },
    showAvatar: {
      type: Boolean,
      default: false
    },
    showFullName: {
      type: Boolean,
      default: false
    },
    reversed: {
      type: Boolean,
      default: false
    }
  },
  name: "headline-message",
  methods: {
    dateFilter
  }
});
