import Axios from "axios";
import { Actions, Getters, Module, Mutations } from "vuex-smart-module";

class GroupState {
  groups: any[] | null = null;
}

class GroupMutations extends Mutations<GroupState> {
  setGroups(payload: any[]) {
    this.state.groups = payload;
  }

  addGroup(payload: any) {
    if (this.state.groups === null) {
      this.state.groups = [];
    }

    this.state.groups?.push(payload);
  }
}

class GroupGetters extends Getters<GroupState> {}

class GroupActions extends Actions<GroupState, GroupGetters, GroupMutations> {
  async createGroup({
    jwtAccessToken,
    groupAccessToken,
    groupId
  }: any): Promise<any> {
    const res = await Axios.post(
      `http://127.0.0.1:3000/group/`,
      {
        groupAccessToken,
        vkGroupId: groupId
      },
      {
        responseType: "json",
        headers: {
          Authorization: "Bearer " + jwtAccessToken
        }
      }
    );

    return res.data;
  }
}

export const group = new Module({
  state: GroupState,
  actions: GroupActions,
  mutations: GroupMutations,
  getters: GroupGetters
});
