<template>
  <div class="select__wrapper">
    <div v-if="isOpen" class="select__option-wrapper">
      <span
        class="select__option"
        v-for="option of options"
        :key="option.value"
        @click="onValueChanged(option)"
      >
        {{ option.text }}
      </span>
    </div>
    <input
      class="select__input"
      :class="{ active: isOpen }"
      ref="input"
      readonly
      :value="selectedOption?.text || ''"
      @click="onClicked"
    />
    <svg
      aria-hidden="true"
      class="select__icon"
      role="img"
      viewBox="0 0 320 512"
    >
      <path
        d="M143 352.3L7 216.3c-9.4-9.4-9.4-24.6 0-33.9l22.6-22.6c9.4-9.4 24.6-9.4 33.9 0l96.4 96.4 96.4-96.4c9.4-9.4 24.6-9.4 33.9 0l22.6 22.6c9.4 9.4 9.4 24.6 0 33.9l-136 136c-9.2 9.4-24.4 9.4-33.8 0z"
      ></path>
    </svg>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export enum ThemeItem {
  default = "default"
}

export interface Option {
  value: unknown;
  text: string;
}

interface SelectData {
  selectedOption: null | Option;
  isOpen: boolean;
}

export default defineComponent({
  name: "headline-select",
  props: {
    options: {
      type: Array as () => Option[],
      required: true
    }
  },
  data() {
    return {
      selectedOption: null,
      isOpen: false
    } as SelectData;
  },
  methods: {
    onClicked() {
      this.isOpen = !this.isOpen;
    },
    onValueChanged(option: Option) {
      this.selectedOption = option;
      this.onClicked();
      this.$emit("change", option.value);
    }
  }
});
</script>

