<template>
  <div
    @click="onClicked"
    ref="div"
    class="dropdown-button"
    :class="{ active: isClicked, opened: isOpen }"
  >
    <slot name="main-button"></slot>
    <button @click.stop="toggleOpen" class="dropdown-button__dropdown-button">
      <svg
        aria-hidden="true"
        class="dropdown-button__dropdown-icon"
        role="img"
        viewBox="0 0 320 512"
      >
        <path
          d="M143 352.3L7 216.3c-9.4-9.4-9.4-24.6 0-33.9l22.6-22.6c9.4-9.4 24.6-9.4 33.9 0l96.4 96.4 96.4-96.4c9.4-9.4 24.6-9.4 33.9 0l22.6 22.6c9.4 9.4 9.4 24.6 0 33.9l-136 136c-9.2 9.4-24.4 9.4-33.8 0z"
        ></path>
      </svg>
    </button>
    <div
      @click.stop="toggleOpen"
      v-if="isOpen"
      class="dropdown-button__dropdown-wrapper"
    >
      <slot name="dropdown-buttons"></slot>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "headline-dropdown-button",
  data() {
    return {
      isOpen: false,
      isClicked: false
    };
  },
  methods: {
    onClicked() {
      this.isClicked = true;
      (this.$refs as any).div.addEventListener("animationend", () => {
        this.isClicked = false;
      });
    },
    toggleOpen() {
      this.isOpen = !this.isOpen;
    }
  }
});
</script>

