import Axios from "axios";
import { Actions, Getters, Module, Mutations } from "vuex-smart-module";

class AuthState {
  accessToken: string | null = null;
}

class AuthMutations extends Mutations<AuthState> {
  setAccessToken(payload: string) {
    this.state.accessToken = payload;
  }
}

class AuthGetters extends Getters<AuthState> {}

class AuthActions extends Actions<AuthState, AuthGetters, AuthMutations> {
  login() {
    window.location.href =
      "https://oauth.vk.com/authorize?client_id=7643784&redirect_uri=http://localhost:8080/&scope=offline,wall&response_type=code";
  }

  async fetchAccessToken(code: string) {
    const res = await Axios.get("http://127.0.0.1:3000/user/access", {
      params: {
        code
      },
      responseType: "json"
    });

    return res.data;
  }
}

export const auth = new Module({
  state: AuthState,
  actions: AuthActions,
  mutations: AuthMutations,
  getters: AuthGetters
});
